
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


// header-componets

.main-header{
    display: flex;
    align-items: flex-start;
    justify-content: center;
    max-width: 100%;
    width: 100%;
    height: 182px;
    background-color: #E7F0FF;
    padding-top: 33px;

    .max-width-header{
        display: flex;
        align-items: center;
        justify-content:center;
        flex-direction: column;
        max-width: 1100px;
        width: 100%;
        padding-inline: 10px;

        .top-header{
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;

            input{
                max-width: 438px;
                width: 100%;
                height: 59px;
                border-radius: 10px;
                outline: none;
                border: none;
                text-indent: 10px;
                color: black;
                margin-left: auto;
                margin-right: 85px;
            }

            .sign-in{
                width: 152px;
                height: 60px;
                background: #00439C;
                color: #ffffff;
                border-radius: 10px;
                border: none;
                cursor: pointer;
                &:hover{
                    opacity: 0.9;
                }
            }

            .menu{
                display: none;
                background: transparent;
                border: none;
                cursor: pointer;

            }
        }

        .lower-header{
            display: flex;
            align-items: center;
            justify-content: space-between;
            max-width: 627px;
            width: 100%;
            height: 27px;
            margin-top: 27px;

            ul{
                display: flex;
                align-items: center;
                justify-content: space-between;
                list-style: none;
                width: 100%;
                li{
                    font-size: 18px;
                    font-weight: 500;
                    cursor: pointer;
                    height: 30px;

                    a{
                        color: #1E1E1E;
                        text-decoration: none;
                        &:hover{
                            color: #00439C;
                            border-bottom: #00439C solid 3px;
                         }
                    }
                }

                select{
                    width: 100px;
                    background: transparent;
                    border: none;
                    font-size: 18px;
                    font-weight: 600;
                    color: #1E1E1E;
                    cursor: pointer;
                    outline: none;
                    &:hover{
                        color: #00439C;
                     }
                }
            }
        }

    }
}


// new--realease


.new--realease{
    width: 100%;
    min-height: 926px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    padding-top: 50px;
    background-color: #E7F0FF;

    h2{
        font-size: 30px;
        font-weight: bold;
        color: #00439C;
    }
    p{
        margin-top: 20px;
        font-size: 21px;
        font-weight: 500;
        color: #00439C;
    }

    .card-new-realease{
        display: flex;
        align-items: center;
        justify-content: center;
        row-gap: 30px;
        column-gap: 30px;
        flex-wrap: wrap;
        margin-top: 64px;
        max-width: 1100px;
        width: 100%;
        min-height: 696px;
        padding: 0 20px;
    }
}

.card-container{
    max-width: 242px;
    width: 100%;
    min-height: 333px;
    background-color: #00439C;
    color: #ffffff;
    border-radius: 20px;
    padding-bottom: 10px;
    

    img{
        width: 240px;
        height: 207px;
    }

    h2{
        margin-top: 10px;
        color: #ffffff;
        font-size: 18px;
        font-weight: 600;
        padding: 0 0 0 10px;
    }
    p{
        color: #ffffff;
        padding: 0 0 0 10px;
    }
}


// coming soon product section

.coming-soon-container{
    width: 100%;
    min-height: 691px;
    background-color: #E7F0FF;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    padding-top: 65px;

    h2{
        font-size: 30px;
        font-weight: bold;
        color: #00439C;
    }
    p{
        margin-top: 20px;
        font-size: 21px;
        font-weight: 500;
        color: #00439C;
    }

    .comingSoon-card{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        row-gap: 30px;
        column-gap: 30px;
        max-width: 1100px;
        width: 100%;
        min-height: 550px;
        margin-top: 30px;
    }
}

.card-soon-container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    row-gap: 10px;
    width: 250px;
    height: 246px;

    h2{
        font-size: 18px;
        font-weight: 600;
    }
}


// product section

.productFull{
    width: 100%;
    margin: 0 auto;
    background-color: #E7F0FF;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 30px 10px;

    div{
        max-width: 1100px;
        width: 100%;
        min-height: 399px;
        padding: 0px  0 0;

        h2{
            font-size: 30px;
            font-weight: bold;
            color: #00439C;
        }
        p{
            margin-top: 20px;
            font-size: 21px;
            font-weight: 500;
            color: #00439C;
        }

        .card-product{
            width: 100%;
            min-height: 300px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-wrap: wrap;
            row-gap: 10px;
            column-gap: 10px;
            padding: 10px;

            .product-cards{
                display: flex;
                align-items: center;
                justify-content: flex-start;
                flex-direction: column;
                text-align: center;
                width: 243px;
                background-color: #ffffff;
                padding: 20px 0;
                border-radius: 10px;
                row-gap: 5px;

                h2{
                    font-size: 16px;
                    font-weight: bold;
                }

                p{
                    font-size: 16px;
                }

              .container-product{
                padding: 0;
                width: 100%;
                min-height: 32px;
                display: flex;
                align-items: center;
                justify-content: space-between;

                .product-left,.product-right{
                    padding: 0;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-direction: column;
                    width: 90%;
                    min-height: 32px;
                }


                .product-left{
                    p{
                        font-size: 14px;
                        font-weight: bold;
                    }
                }
                .product-right{
                    button{
                        width: 76px;
                        height: 32px;
                        background-color: #00439C;
                        color: #ffffff;
                        border: none;
                        cursor: pointer;
                        border-radius: 10px;
                        &:hover{
                            opacity: 0.9;
                        }
                    }
                }

              }
            }
        }
    }


}

// blog section


.blog-main-container{
    width: 100%;
    min-height: 691px;
    background-color: #E7F0FF;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    padding-top: 65px;

    .blog-max-container{
        max-width: 1100px;
        width: 100%;
        min-height: 399px;

        h2{
            font-size: 30px;
            font-weight: bold;
            color: #00439C;
        }

        .blog-card-container{
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-wrap: wrap;
            row-gap: 20px;
            column-gap: 20px;
            margin-top: 30px;
            padding-bottom: 20px;

            .blog-left{
                width: 343px;
                height: 535px;
                display: flex;
                align-items: center;
                justify-content: center;
                row-gap: 30px;
                column-gap: 20px;

                .blog-main-card{
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-direction: column;
                    width: 338px;
                    min-height:399px;
                    background-color: #ffffff;
                    border-radius: 10px;
                    padding-bottom: 20px;

                    .img-blog{
                        width: 100%;
                        height: 274px;
                        background-color: #00439C;
                        border-radius: 10px;

                        img{
                            width: 330px;
                            height: 270px;
                        }
                    }
                    h2{
                        margin-top: 30px;
                        text-align: center;
                        font-size: 18px;
                        font-weight: bold;
                        color: #00439C;
                    }
                    p{
                        text-align: center;
                        font-size: 14px;
                        font-weight: bold;
                        color: #00439C;
                        margin-top: 30px;
                        padding: 0 10px;
                    }
                }
            }
            .blog-right{
                width: 705px;
                min-height: 537px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                flex-wrap: wrap;

                .blog-news{
                    width: 338px;
                    height: 255px;
                }
            }
        }
    }
}


// footer

.footer-main-container{
    width: 100%;
    min-height: 409px;
    background-color: #00439C;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    padding: 50px 10px;

    .footer-max-container{
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        row-gap: 40px;
        column-gap: 20px;
        max-width: 1100px;
        width: 100%;
        min-height: 355px;

        .footer-left{
            display: flex;
            align-items: flex-start;
            justify-content: flex-start;
            flex-direction: column;
            row-gap: 15px;
            width: 373px;
            min-height: 239px;

            img{
                width: 234px;
                height: 68px;
            }
            p{
                color: #ffffff;
                font-size: 12px;
                font-weight: 300;
            }

            .footer-details{
                h2{
                    font-size: 16px;
                    font-weight: bold;
                    color: #ffffff;
                    margin-bottom: 20px;
                }

                .footer-list{
                    list-style: none;
                    color: #ffffff;
                    display: flex;
                    align-items: flex-start;
                    justify-content: flex-start;
                    flex-direction: column;
                    row-gap: 10px;
                    li{
                        font-size: 12px;
                        span{
                            img{
                                width: 15px;
                                height: 12px;
                                margin-right: 10px;
                            }
                        }
                    }
                }
            }
        }
        .footer-right{
            width: 580px;
            min-height: 239px;
            display: flex;
            align-items: flex-start;
            justify-content: space-between;

            .footer-nav-list{
                width: 130px;
                min-height: 238px;
                h2{
                    font-size: 16px;
                    font-weight: bold;
                    color: #ffffff;
                }

                ul{
                    list-style: none;
                    li{
                        color: #ffffff;
                        font-size: 10px;
                        margin-top: 15px;
                       a{
                        text-decoration: none;
                        color: #ffffff;
                        &:hover{
                            border-bottom: #ffffff solid 1px;
                        }
                       }
                    }
                }
            }
        }
    }
}


